/* Results container */

.results-container {
	margin-top: 22vh;
}

.results-container .tilesWrap {
	padding: 0;
	margin: 0;
	list-style: none;
	text-align: center;
	z-index: 0;
	position: relative;
}
.results-container .tilesWrap li {
	display: inline-block;
	width: 20%;
	min-width: 200px;
	max-width: 230px;
	padding: 80px 20px 40px;
	position: relative;
	vertical-align: top;
	margin: 10px;
	font-family: "helvetica", san-serif;
	min-height: 25vh;
	background: #262a2b;
	border: 1px solid #252727;
	text-align: left;
}
.results-container .tilesWrap li h2 {
	font-size: 114px;
	margin: 0;
	position: absolute;
	opacity: 0.2;
	top: 50px;
	right: 10px;
	transition: all 0.3s ease-in-out;
}
.results-container .tilesWrap li h3 {
	font-size: 20px;
	color: #b7b7b7;
	margin-bottom: 5px;
}
.results-container .tilesWrap li p {
	font-size: 16px;
	line-height: 18px;
	color: #b7b7b7;
	margin-top: 5px;
}
.results-container .tilesWrap li button {
	background: transparent;
	border: 1px solid #b7b7b7;
	padding: 10px 20px;
	color: #b7b7b7;
	border-radius: 3px;
	position: relative;
	transition: all 0.3s ease-in-out;
	transform: translateY(-40px);
	opacity: 0;
	cursor: pointer;
	overflow: hidden;
}
.results-container .tilesWrap li button:before {
	content: "";
	position: absolute;
	height: 100%;
	width: 120%;
	background: #b7b7b7;
	top: 0;
	opacity: 0;
	left: -140px;
	border-radius: 0 20px 20px 0;
	z-index: -1;
	transition: all 0.3s ease-in-out;
}
.results-container .tilesWrap li:hover button {
	transform: translateY(5px);
	opacity: 1;
	z-index: 2;
}
.results-container .tilesWrap li button:hover {
	color: #262a2b;
}
.results-container .tilesWrap li button:hover:before {
	left: 0;
	opacity: 1;
}
.results-container .tilesWrap li:hover h2 {
	top: 0px;
	opacity: 0.6;
}

.results-container .tilesWrap li:before {
	content: "";
	position: absolute;
	top: -2px;
	left: -2px;
	right: -2px;
	bottom: -2px;
	z-index: -1;
	background: #fff;
	transform: skew(2deg, 2deg);
}
.results-container .tilesWrap li:after {
	content: "";
	position: absolute;
	width: 40%;
	height: 100%;
	left: 0;
	top: 0;
	background: rgba(255, 255, 255, 0.02);
}
.results-container .tilesWrap .virus:before {
	background: #9dff89;
	background: linear-gradient(to right, #ffafbd, #9dff89);
}

.results-container .tilesWrap .disease:before {
	background: #6bafd5;
	background: linear-gradient(to right, #ffafbd, #6bafd5);
}

